<template>
  <div class="home common-page">
    <div class="music-group">
      <music class="music" :offPath="offPath" :onPath="onPath" :musicPath="musicPath"></music>
      <div class='music-count'>{{two}}</div>
    </div>

    <img class="content-img" src="../assets/cosleep_share_img_window@3x.png">

    <div class="icon-list">
      <div class="icon" v-for="li in tubiao">
        <img @click="" :src="li.resurl"/>
        <p>{{li.musicdesc}}</p>
      </div>
    </div>

    <div class="user">
      <img :src="user_pic"/><p>{{user_name}}</p>
    </div>

    <div class="desc">
      <div class="desc-cont">
        {{share_article_content}}
      </div>
      <div class="shadow"></div>
    </div>

   <!-- <a class="dl"  target="view_window" href="https://www.psy-1.com/app/redirect?code=10010&music_detail_code=fMLCDTbHUBkvUFUA#/" > -->
      <div class="open" @click="openApp">
        打开App体验完整版组合
      </div>
   <!-- </a>-->

  </div>
</template>

<script>

  global_type = 0
import music from '@/components/music.vue'
import shareTool from '@/components/shareandgetname.js'

function getQueryString(name) {
    var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    var r = window.location.search.substr(1).match(reg);
    if (r != null) return unescape(r[2]); return null;
}



export default {
  name: 'home',
  data () {
      return {
          offPath:'https://res.psy-1.com/music/voice/tinysleep_sheep_play2-BHgSoY5B6WFClAfSEh2i.png',
          onPath:'https://res.psy-1.com/music/voice/tinysleep_sheep_stop2-jwlPPhdRF58oErMNroV2.png',
          musicPath:'',
          code: getQueryString('code').replace(/#/,'').replace(/\//, ''),
          two: '',
          user_pic: '', //这是头像
          user_name: '', //这是名称
          tubiao: {},
          share_article_content: '',
          share_article_title: '',
          type:'',
          article_id: 10010,
        share_article_id:''

      }
  },
  methods: {
      getData(url){
          let type = 0, st = '/web/v1/cosleep/forum/article/share/query'
          try{
              type = getQueryString('type').replace(/#/,'').replace(/\//, '')
          }catch(e){
              console.log('没有')
              type = 0
          }


          type == 2 ? st = '/web/v1/cosleep/forum/article/share/query' : st = "/web/v1/cosleep/music/share/query"
          if(type == 2){
            global_type = 2
          }

          console.log(st)

          $.get(url + st,{code: getQueryString('code').replace(/#/,'').replace(/\//,'')},
          (data)=>{
              //document.querySelector('body').backgroundColor = data.data.share_music_color  //修改背景颜色

              //获取music
              this.musicPath = data.data.share_music_combine
              document.querySelector('#myaudio').setAttribute('src',this.musicPath)
              get.boolean = true
        if(type == 2){
          this.share_article_id = data.data.share_article_id
        }


              //用户的头像名称的获取和设置
              const user = JSON.parse(data.data.share_user_info)
              this.user_name = user.name
              this.user_pic = user.avatar

              //获取图标
              const tubiao =  eval('('+data.data.share_music_list+')')
              this.tubiao = tubiao
              console.log(tubiao)

              //设置标题
              this.share_article_title = data.data.share_article_title
              if(type == 0) {
                  this.share_article_title = "今晚点它,一夜好觉"
                  document.title =  "今晚点它 一夜好觉"
              }else{
                  this.share_article_title = "失眠专用"
                  document.title =  "失眠专用"
              }


              //获取描述content
              this.share_article_content = data.data.share_article_content


              //分享设置
              let title_list = ''
              for(let li of tubiao){
                  console.log(li)
                  title_list += li.musicdesc + "+"
              }
              title_list = title_list.replace(/\+$/,'')
              this.share('http://wx1.sinaimg.cn/mw690/006Zdy2vgy1fwjdfkq0thj305k05k0nx.jpg'
              , this.share_article_title, title_list, data.data.share_music_combine)


          })
      },
      musicDetail(){
          let copy = []
          for(let i in this.tubiao){
              copy.push({id: this.tubiao[i].id, music_volume: this.tubiao[i].music_volume, music_rate: this.tubiao[i].music_rate})
          }
          //console.log(copy)


          let list = ''
          const sb = copy
          for(let i in sb){
              // [" + Object.keys(sb[i])[0] + "]=" + Object.values(sb[i])[0] + "&"
              for(let z in sb[i]){
                  list +=  "music_detail["+ i +"][" + z + "]=" +  sb[i][z] + "&"
              }
          }
          return list.replace(/&$/,'')
      },
      musicCount(){
          const a = Math.round(document.querySelector('#myaudio').duration) - Math.round(document.querySelector('#myaudio').currentTime)
          this.two = this.formatSeconds(a)
      },
      formatSeconds(value) {
          var secondTime = parseInt(value);// 秒
          var minuteTime = 0;// 分
          var hourTime = 0;// 小时
          if(secondTime > 60) {//如果秒数大于60，将秒数转换成整数
              //获取分钟，除以60取整数，得到整数分钟
              minuteTime = parseInt(secondTime / 60);
              //获取秒数，秒数取佘，得到整数秒数
              secondTime = parseInt(secondTime % 60);
              //如果分钟大于60，将分钟转换成小时
              if(minuteTime > 60) {
                  //获取小时，获取分钟除以60，得到整数小时
                  hourTime = parseInt(minuteTime / 60);
                  //获取小时后取佘的分，获取分钟除以60取佘的分
                  minuteTime = parseInt(minuteTime % 60);
              }
          }
          let mark = ''
          if(parseInt(secondTime) < 10){
              mark = '0'
          }
          var result = mark + parseInt(secondTime) + " ";

          let mark2 = '0'

          result = mark2 + parseInt(minuteTime) + ":" + result;

          if(hourTime > 0) {
              result = "" + parseInt(hourTime) + "小时" + result;
          }
          return  result;
      },
      share(pic, title, desc,dataUrl) {
          const share = new shareTool({pic:"http://wx1.sinaimg.cn/mw690/006Zdy2vgy1fwjdfkq0thj305k05k0nx.jpg", url:window.location.href.split('#')[0], title, desc, dataUrl})
          share.rawWeiXinShare('https://api.debug.psy-1.com')
          if(this.$store.getters.isApp)  share.appInit()
      },
      openApp(){
          if(!this.$store.getters.isWeiXin){
              this.appOpenSet()
          }else{
              this.$parent.tips_toggle = true
              setTimeout(()=>{
                  this.$parent.tips_toggle = false
              },1000)
          }

      },
      appOpenSet(){
          const that = this
          var  createIframe=(function(){
              var iframe;
              return function(){
                  if(iframe){
                      return iframe;
                  }else{
                      iframe = document.createElement('iframe');
                      iframe.style.display = 'none';
                      document.body.appendChild(iframe);
                      return iframe;
                  }
              }
          })()

          var baseScheme = "cosleep://"
          var createScheme=function(options){
              var urlScheme=baseScheme;
              for(var item in options){
                  urlScheme=urlScheme+item + '=' + encodeURIComponent(options[item]) + "&";
              }
              urlScheme = urlScheme.substring(0, urlScheme.length - 1);
              return encodeURIComponent(urlScheme);
          }

          var openApp=function(){
              var localUrl=createScheme();
              var openIframe=createIframe();
              if(/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
                  //判断是否是ios,具体的判断函数自行百度
                  //alert("cosleep://deeplink?code=10010&music_detail_code="+that.code)
                  console.log(decodeURI("cosleep://deeplink?code=10010&music_detail_code="+that.code +"&"+ encodeURI( that.musicDetail() )))

                if(global_typel == 2){
                  window.location.href = "cosleep://deeplink?code=10010&music_detail_code="+that.share_article_id
                }else{
                  window.location.href = "cosleep://deeplink?code=10010&music_detail_code="+that.code // +"&"+ encodeURI( that.musicDetail() )

                }

                var loadDateTime = Date.now();
                  setTimeout(function () {
                      var timeOutDateTime = Date.now();
                      if (timeOutDateTime - loadDateTime < 1000) {
                         // window.location.href = "cosleep://deeplink?code=10010&music_detail_code="+that.code
                          //window.location.href = 'cosleep://deeplink?code=10010&music_detail_code=kZkCCukLd3CHWMWZ'
                          console.log(decodeURI("cosleep://deeplink?code=10010&music_detail_code="+that.code +"&"+ encodeURI( that.musicDetail() )))

                        if(global_typel == 2){
                          window.location.href = "cosleep://deeplink?code=10010&music_detail_code="+that.share_article_id
                        }else{
                          window.location.href = "cosleep://deeplink?code=10010&music_detail_code="+that.code // +"&"+ encodeURI( that.musicDetail() )

                        }


                      }
                  }, 25);
              }else if(!/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)){
                  //判断是否是android，具体的判断函数自行百度
                  if (false) {
                      //chrome浏览器用iframe打不开得直接去打开，算一个坑
                      window.location.href = localUrl;
                  } else {
                      //抛出你的scheme
                      openIframe.src = localUrl;
                  }
                  setTimeout(function () {
                      //alert("cosleep://deeplink?code=10010&music_detail_code="+this.code)

                    if(global_typel == 2){
                      window.location.href = "cosleep://deeplink?code=10010&music_detail_code="+that.share_article_id
                    }else{
                      window.location.href = "cosleep://deeplink?code=10010&music_detail_code="+that.code // +"&"+ encodeURI( that.musicDetail() )

                    }


                      //window.location.href = 'cosleep://deeplink?code=10010&music_detail_code=kZkCCukLd3CHWMWZ'
                  }, 500);
              }else{
                  //主要是给winphone的用户准备的,实际都没测过，现在winphone不好找啊
                  openIframe.src = localUrl;
                  setTimeout(function () {
                      window.location.href = "https://www.heartide.com/download/smallsleep";
                  }, 500);
              }
          }
          openApp()
      }
  },
  components: {
      music
  },
  mounted(){

      //下次有空封装好哈 毕竟这样比较丑~
      document.querySelector('#myaudio').addEventListener('canplaythrough',()=>{
          this.musicCount()
          setInterval(this.musicCount, 1000)
      })

      //获取信息
      this.getData('https://api.debug.psy-1.com')

      //音乐播放完毕判断
      document.querySelector('#myaudio').addEventListener('ended',()=>{
          this.$parent.show = true
      })


  }

}
</script>

